.picture {
  display: inline-block;
  position: relative;
}

.picture-frame {
  z-index: 9999;
  background-color: #DA4167;

  transition: background-color 0.2s;
}

.picture-frame:hover {
  background-color: #DA4167;
}

.picture-shadow {
  z-index: -1;

  content: "";
  position: absolute;

  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  border-color: #DA4167;
  border-style: solid;
  border-width: 3px;
  -webkit-transform: translate(15px, 15px);
          transform: translate(15px, 15px);
}

.picture-frame:hover+.picture-shadow {
  -webkit-transform: translate(10px, 10px);
          transform: translate(10px, 10px);
}

.me-picture {
  opacity: 0.5;
  -webkit-filter: brightness(110%) contrast(105%) sepia(20%)hue-rotate(-30deg);
          filter: brightness(110%) contrast(105%) sepia(20%)hue-rotate(-30deg);

  transition: -webkit-filter 0.2s;

  transition: filter 0.2s;

  transition: filter 0.2s, -webkit-filter 0.2s;
}

.me-picture:hover {
  opacity: 1;
  -webkit-filter: brightness(110%) contrast(105%) sepia(20%);
          filter: brightness(110%) contrast(105%) sepia(20%);
}











